import {useLocation, useNavigate, Navigate} from "react-router-dom";
import {useAuth} from "./AuthProvider";
import {Fragment, useEffect, useState} from "react";
import authService from "../Services/AuthenticationService";
import Endpoints from "../Endpoints";

export function AuthorizeRoute({children, ...props})
{
    const [authenticated, setAuthenticated] = useState(false);
    const [ready, setReady] = useState(false);
    let navigate = useNavigate();

    useEffect(() => {
        async function fetchInfo()
        {
            setAuthenticated(await authService.isAuthenticated());
            setReady(true);
        }

        const handleId = authService.subscribe(async () => {
            setReady(false);
            setAuthenticated(false);
            await fetchInfo();
        });

        fetchInfo();

        return () => authService.unsubscribe(handleId);
    }, [])

    return !ready ? <div></div> : <Fragment>
        {authenticated ? children : <Navigate to={`${Endpoints.Auth.Login}?returnUrl=${encodeURIComponent(window.location)}`}/>}
    </Fragment>;
}