import {Fragment, useEffect, useState} from "react";
import authService from "../Services/AuthenticationService";
import {useNavigate} from "react-router-dom";
import Endpoints from "../Endpoints";

export default function ProfileFragment(props)
{
    const [user, setUser] = useState({
        username: "Cenk Ergen"
    });
    let navigate = useNavigate();

    useEffect(() => {
        async function fetchInfo()
        {
            setUser(await authService.getUser());
        }

        const handleId = authService.subscribe(async () => {
            await fetchInfo();
        });

        //fetchInfo();

        return () => authService.unsubscribe(handleId);
    }, [])

    return <Fragment>
        {!user ? <div>
            <a href={`${Endpoints.Auth.Prefix}/${Endpoints.Auth.Login}`} className="btn btn-outline-light me-2">Login</a>
            <a href={`${Endpoints.Auth.Prefix}/${Endpoints.Auth.Login}`} className="btn btn-light">Register</a>
        </div> : <div className="nav-item dropdown text-light">
            <a className="nav-link dropdown-toggle" href="#" id="profileDropdownMenuLink" role="button" data-bs-toggle="dropdown">
                <span className="material-symbols-outlined align-middle">account_circle</span>
                {user.username}
            </a>
            <ul className="dropdown-menu">
                <li><a className="dropdown-item">Profile</a></li>
                <li><a className="dropdown-item">Courses</a></li>
                <li><a className="dropdown-item text-danger">Logout</a></li>
            </ul>
        </div> }
    </Fragment>;
}